function setStatusBarStyleLight() {
  try {
    StatusBar.setStyle({ style: Style.Dark });
    console.log("HALOO");
  } catch (error) {
    console.log(error);
  }
}

onMounted(() => {
  buatKoneksi();
  setStatusBarStyleLight();
});
</script>

