import { defineStore } from 'pinia'

export const dataKontrolTampilanPengguna = defineStore({
  id: "data.kontrol.tampilan.pengguna",
  state: () => {
    return { 
        tampilan_pengguna: 'monitor'
    }
  },
  actions: {
    ubahTampilan(tampilan){
        this.tampilan_pengguna = tampilan;
    }
  }
})